<template>
<div class="nav-wrapper">
  <div class="nav">
    <div class="nav__brand">
      <img class="nav_brand--logo" src="../assets/logo.png" />
    </div>
    <div class="nav__searchbar">
      <app-searchbar />
    </div>
    <ul class="nav__menu">
      <li v-for="item in menuList" v-bind:key="item" class="item">
        {{ item }}
      </li>
      <li class="item sp">로그인</li>
      <span>|</span>
      <li class="item sp">회원가입</li>
    </ul>
  </div>
  <ul class="sub-nav">
    <li v-for="item in subMenuList" v-bind:key="item" class="item">
      {{ item }}
    </li>
  </ul>
</div>
</template>

<script>
import Searchbar from '@/components/Searchbar'

export default {
  name: 'Nav',
  components: {
    'app-searchbar': Searchbar
  },
  data () {
    return {
      menuList: [ '공지사항', '서비스 설명', 'FAQ', '광고 문의' ],
      subMenuList: [ '파워 체험단', '당일마감 체험단', '최신 체험단', '제품배송 체험단', '이벤트' ]
    }
  }
}
</script>

<style scoped>
.nav-wrapper {
  background-color: #fff;
}
.nav {
  padding: 0px 6.25vw 0;
  height: 80px;
  border-bottom: 1px solid #ddd;
  line-height: 80px;
}
.nav__brand {
  display: inline;
  font-size: 1.5rem;
  font-weight: 800;
  cursor: pointer;
}
.nav_brand--logo {
  display: inline-block;
  width: 150px;
  vertical-align: middle;
}
.nav__searchbar {
  display: inline-block;
  padding-left: 20px;
}
.nav__menu {
  list-style: none;
  display: inline;
  float: right;
  padding: 0;
  margin: 0;
}
.nav__menu > .item {
  display: inline;
  margin: 0 15px;
  cursor: pointer;
}

.nav__menu > .item.sp {
  color: #d13738;
  margin: 0 3px;

}
.nav__menu > span {
  color: #ccc;
}
.sub-nav {
  text-align: center;
  border-bottom: 1px solid #ccc;
  height: 50px;
  line-height: 50px;

  list-style: none;
  padding: 0;
  margin: 0;
}

.sub-nav > .item {
  display: inline;
  margin: 0 20px 0 0;
  cursor: pointer;
  font-weight: 800;
}

.sub-nav > .item:hover {
  color: #d13738;
}

.sub-nav > .item:after {
  content: '|';
  margin-left: 20px;
  color: #ccc;
  font-weight: 400;
}
.sub-nav > .item:last-child:after {
  content: '';
}
.sub-nav > span {
  color: #ccc;
  font-weight: 400;
}
</style>



