<template>
  <div class="signin">
    <img class="signin__brand" src="../assets/logo.png"/>
    <form class="signin__input--wrapper">
      <input type="text" placeholder="아이디 (이메일)" />
      <input type="password" placeholder="비밀번호" />
      <label for="auto-login">
        <input id="auto-login" type="checkbox" />
        <span>자동 로그인</span>
      </label>
      <span id="find-info">아이디(이메일)/비밀번호 찾기</span>
      <input type="submit" value="로그인" />
      <hr />
      <input type="button" value="회원가입" />
      <p class="copy">Copyright © dingble All rights reserved.</p>
    </form>
  </div>
</template>

<script>

export default {
  name: 'Home',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
hr {
  all: unset;
  display: block;
  border-top: 1px solid #ccc;
  margin: 20px 0 20px 0;
}
.signin {
  background-color: #fff;
  min-height: 100vh;
  width: 360px;
  margin: 50px auto 0 auto;
  text-align: center;
}
.signin__brand {
  width: 200px;
}
.signin__input--wrapper {
  margin-top: 40px;
}
.signin__input--wrapper input {
  cursor: pointer;
  width: 360px;
  height: 50px;
  display: block;
  border: 1px solid #c8c8c8;
  padding: 0 10px;
  margin: 0 auto 15px auto;
  box-sizing: border-box;
  font-weight: 600;
}
.signin__input--wrapper input[type="submit"] {
  background-color: #d13738;
  color: #fff;
}
.signin__input--wrapper input[type="submit"]:active {
    background-color: #c13738;
}
.signin__input--wrapper input[type="checkbox"] {
  display: inline-block;
  width: 16px;
  height: 16px;
  vertical-align: middle;
}
label {
  text-align: left;
  vertical-align: middle;
  font-size: 14px;
  float: left;
}
label span {
  vertical-align: top;
}
#find-info {
  cursor: pointer;
  display: inline;
  font-size: 14px;
  float: right;
  text-align: right;
}
.copy {
  margin: 15px 0;
  font-size: 12px;
}
</style>



