<template>
  <!-- 임시 컴포넌트 -->
  <div class="header">
      <div class="header__title">{{ title }}</div>

    <carousel :data="data" indicator-type="disc"></carousel>
  </div>
</template>

<script>
import Slide2 from '@/components/slides/SampleSlide2'

export default {
  name: 'Header2',
  data () {
    return {
      title: '당일마감 체험단',
      data: [
        Slide2,
        Slide2,
        Slide2
      ]
    }
  }
}
</script>

<style scoped>
  .header {
      position: relative;
      width: 100%;
      min-height: 400px;
  }
  .header__title {
      position: absolute;
      z-index: 10;
      padding-top: 24px;
      padding-left: 110px;
      font-size: 1rem;
      font-weight: 600;
      color: #333;
  }
</style>



