.home {
  background-color: #fff;
  min-height: 100vh;
}
.mypage--wrapper {
  display: inline-block;
  margin: 10px 0px 40px 40px;
  vertical-align: top;
}
.mypage__title {
  display: block;
  margin: 20px 0;
  font-size: 1.2rem;
