<template>
  <div class="card">
    <div class="card__cover--wrapper">
      <img class="card__cover" src="../assets/sample.jpg">
    </div>
    <div class="card__cover--text-wrapper">
      <div class="card__cover--title">{{ title }}</div>
      <div class="card__cover--contents">{{ contents }}</div>
    </div>
    <div class="card--text-wrapper">
      <div class="card__title">
        {{ categori }} {{ title }}
      </div>
      <div class="card__contents">
        <span class="card__contents__type">{{ type }}</span>
        모집인원 <span class="card__contents__status">10/40</span>
        <span class="card__contents__d-day">3일 남음</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Card',
  data () {
    return {
      categori: '[대전/유성]',
      title: '토미야 우동',
      type: '인스타그램',
      contents: '대전 둔산동 우동 맛집!',
      cover: '../assets/sample.jpg'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.card {
  display: inline-block;
  height: 243px;
  width: 277px;
  background-color: #fff;
  border-radius: 5px;
  overflow: hidden;
  cursor: pointer;
  margin: 5px 10px;
}
.card__cover--wrapper {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 180px;
}
.card__cover--wrapper:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(to top, #00000099, #ffb3b300);
}
.card__cover {
  width: 100%;
}
.card__cover--text-wrapper {
  position: absolute;
  margin-top: -60px;
  padding: 0 10px;
}
.card__cover--text-wrapper > * {
  color: #fff;
}
.card__cover--title {
  font-size: 22px;
  margin-bottom: 8px;
}
.card__cover--contents {
  font-weight: 100;
  font-size: 10px;
  opacity: 0.8;
}
.card--text-wrapper {
  padding: 6px 4px 0 2px;
}
.card__title {
    font-size: 16px;
    margin-bottom: 8px;
}
.card__contents {
    font-size: 12px;
}
.card__contents__type {
  display: inline-block;
  color: #d13738;
  width: 65px;
}
.card__contents__d-day {
  float: right;
}
</style>



