<template>
  <div class="searchbar">
    <input type="text"/>
    <font-awesome-icon class="search-icon" icon="search" />
  </div>
</template>

<script src="https://kit.fontawesome.com/2c4691a02f.js" crossorigin="anonymous"></script>

<script>
export default {
  name: 'Searchbar',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>

<style scoped>
.searchbar {
  display: inline-block;
  position: relative;
}

input {
  all: unset;
  display: inline-block;
  width: 240px;
  background-color: #e9e9e9;
  padding: 0 40px 0 20px;
  height: 40px;
  border-radius: 30px;
  font-size: 14px;
}
.search-icon {
  margin-left: -30px;
  cursor: pointer;
}
</style>



