import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/containers/Home'
import Product from '@/containers/Product'
import SignIn from '@/containers/SignIn'
import SignUp from '@/containers/SignUp'
import SignUpAdv from '@/containers/SignUpAdv'
import Mypage from '@/containers/Mypage'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/product',
      name: 'Product',
      component: Product
    },
    {
      path: '/signin',
      name: 'SignIn',
      component: SignIn
    },
    {
      path: '/signup',
      name: 'SignUp',
      component: SignUp
    },
    {
      path: '/signup/adv',
      name: 'SignUpAdv',
      component: SignUpAdv
    },
    {
      path: '/mypage',
      name: 'Mypage',
      component: Mypage
    },
    {
      path: '/',
      name: 'Home',
      component: Home
    }
  ]
})



