<template>
  <div class="PremiumCard">
    <div class="PremiumCard__cover--wrapper">
      <img class="PremiumCard__cover" src="../assets/sample.jpg">
    </div>
    <div class="card--text-wrapper">
      <div class="PremiumCard__title">
        {{ categori }} {{ title }}
      </div>
      <div class="PremiumCard__contents">
        <span class="PremiumCard__contents__type">{{ type }}</span>
        모집인원 <span class="PremiumCard__contents__status">10/40</span>
        <span class="PremiumCard__contents__d-day">3일 남음</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PremiumCard',
  data () {
    return {
      categori: '[대전/유성]',
      title: '토미야 우동',
      type: '인스타그램',
      contents: '대전 둔산동 우동 맛집!',
      cover: '../assets/sample.jpg'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.PremiumCard {
  display: inline-block;
  height: 230px;
  width: 277px;
  background-color: #fff;
  border-radius: 5px;
  overflow: hidden;
  cursor: pointer;
  margin: 10px 10px;
}
.PremiumCard__cover--wrapper {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 150px;
}
.PremiumCard__cover--wrapper:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(to top, #00000099, #ffb3b300);
}
.PremiumCard__cover {
  width: 100%;
}
.card--text-wrapper {
  padding: 20px 10px 0 10px;
}
.PremiumCard__title {
    font-size: 18px;
    margin-bottom: 8px;
}
.PremiumCard__contents {
    font-size: 12px;
}
.PremiumCard__contents__type {
  display: inline-block;
  color: #d13738;
  width: 65px;
}
.PremiumCard__contents__d-day {
  float: right;
}
</style>



