<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
hr {
  all: unset;
  display: block;
  border-top: 1px solid #ccc;
  margin: 20px 0 20px 0;
}
.signup {
  background-color: #fff;
  min-height: 100vh;
  width: 460px;
  margin: 50px auto 0 auto;
  text-align: center;
}
.signup__brand {
