<template>
  <div class="header">
      <div class="header__title">{{ title }}<font-awesome-icon class="question-icon" icon="question-circle" /></div>

    <carousel :data="data" indicator-type="disc"></carousel>
  </div>
</template>

<script>
import Slide from '@/components/slides/SampleSlide'

export default {
  name: 'Header',
  data () {
    return {
      title: '파워 체험단',
      data: [
        Slide,
        Slide,
        Slide
      ]
    }
  }
}
</script>

<style scoped>
.header {
    position: relative;
    width: 100%;
    min-height: 285px;
}
.header__title {
  position: absolute;
  z-index: 10;
  padding-top: 24px;
  padding-left: 110px;
  font-size: 1rem;
  font-weight: 600;
  color: #d13738;
}
.question-icon {
  cursor: pointer;
  font-size: 0.8rem;
  margin-left: 8px;
  opacity: 0.8;
}
.question-icon:hover {
  opacity: 1;
}
</style>



